<template>
  <span>
    <!-- silence is bliss -->
  </span>
</template>

<script>
const component = {
  name: "app-controls",

  mounted() {
    document.onkeydown = this.handleKeyDown;
  },

  methods: {
    handleKeyDown(e) {
      e.preventDefault();
      e.stopPropagation();
      e = e || window.event;
      switch (e.which || e.keyCode) {
        case 32: {
          this.$bus.$emit("key:space");
          break;
        }
        case 37: {
          this.$bus.$emit("key:arrow_left");
          break;
        }
        case 39: {
          this.$bus.$emit("key:arrow_right");
          break;
        }
        default: {
          break;
        }
      }
    }
  }
};

export default component;
</script>


