<template>
  <section @click.stop="toggle">

    <div v-if="selectedPost" @click.stop class="btn-container" >

      <md-button :disabled="!displayBack" class="md-icon-button md-primary md-raised md-fab md-mini" @click="previousPost">
        <md-icon>arrow_back</md-icon>
      </md-button>
        
      <md-button :disabled="!displayNext" class="md-icon-button md-primary md-raised md-fab md-mini" @click="nextPost">
        <md-icon>arrow_forward</md-icon>
      </md-button>

      <a :href=" selectedPost.image " :download=" selectedPost.name ">
        <md-button class="md-icon-button md-primary md-raised md-fab md-mini">
          <md-icon>file_download</md-icon>
        </md-button>
      </a>
      
      <md-button class="md-icon-button md-primary md-raised md-fab md-mini" @click="toggle">
        <md-icon>menu</md-icon>
      </md-button>

    </div>
    
    <nav v-if="show">
      <ul @click.stop>
        <li v-for="(item, index) in posts" :key="index" @click="handleClick(index)">
          <md-button>{{ index + 1 }}</md-button>
        </li>
      </ul>
    </nav>

  </section>
</template>


<script>
import PostsConnect from "../containers/Posts";

const component = {
  name: "app-navigation",

  data: () => ({ show: false }),

  methods: {
    toggle() {
      this.show = !this.show;
    },

    handleClick(index) {
      this.toggle();
      this.selectPostByIndex(index);
    }
  }
};

export default PostsConnect(component);
</script>

<style scoped lang="scss">
section {
  .btn-container {
    position: absolute;
    top: 10px;
    right: 5px;
    overflow: visible;
  }

  nav {
    background-color: rgba(0, 0, 0, 0.5);
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;

    ul,
    li {
      list-style: none;
      padding: 0;
      margin: 0;
    }

    ul {
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      max-width: 100vw;
      max-height: 80vh;
      width: 300px;
      height: 300px;
      background-color: white;
      overflow-y: scroll;
      padding: 5px;
      border-radius: 4px;

      li {
        display: inline-block;
        width: calc(100% / 5 - 10px);
        margin: 5px;

        button {
          font-weight: 600;
          margin: 0;
          width: 100%;
          padding: 0;
          min-width: 0;
        }
      }
    }
  }

  > * {
    overflow: -moz-scrollbars-none;
    -ms-overflow-style: none;

    ::-webkit-scrollbar {
      width: 0 !important;
    }
  }
}
</style>


